.v-toolbar {
	position: absolute;
	z-index: 500;
	top: 0;
	left: 0;
	right: 0;
	padding: 7px 20px 6px;
	border-bottom: 1px solid #ebccd1;
	height: 54px;
	color: #a94442;
	background-color: #f2dede;
	&[style*="display: none;"],
	&[style*="display:none;"] {
		+ .main-container {
			padding-top: 0; } }
	+ .main-container {
		padding-top: 54px; }
	&__btn {
		position: static !important;
		display: inline-block;
		vertical-align: top;
		// float: right
		margin: -11px 10px;
		height: 40px;
		outline: none !important; }
	&__content {
		overflow: hidden;
		padding: 11px 0;
		font-size: 16px;
		line-height: 18px;
		p {
			display: inline-block;
			vertical-align: top;
			padding: 0;
			margin: 0; } } }
