.g-box {
	position: relative;
	height: 100%;
	&_form,
	&_success {
		position: absolute;
		z-index: 501;
		top: 135px;
		left: 0;
		right: 0;
		bottom: 0;
		height: auto;
		min-height: 465px;
		opacity: 0;
		visibility: hidden;
		background: #f8f8f8;
		transition: opacity 0.25s, visibility 0.25s;
		&.is-open {
			opacity: 1;
			visibility: visible; }
		.v-guide-form {
			padding-left: 0;
			padding-right: 0;
			box-shadow: none; } }
	//footer elements
	&__footer {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 20px;
		font-size: 0;
		text-align: center;
		+ .g-box__inner {
			padding-bottom: 100px; }
		&-row {
			margin: -10px; }
		&-col {
			display: inline-block;
			vertical-align: top;
			padding: 10px;
			@include sm {
				width: 100%; } } }
	&__btn {
		width: 400px;
		@include md {
			width: 260px; }
		@include sm {
			max-width: 100%; } }
	&__back {
		position: absolute;
		left: 40px;
		bottom: 31px;
		@include md {
			left: 20px; }
		@include sm {
			bottom: 20px; }
		+ .g-box__footer-row {
			@include lg {
				margin: -10px auto;
				max-width: 1044px;
				text-align: right; }
			@include md;
			@include sm {
				max-width: 520px;
				padding-left: 53px; }
			@include r(500) {
				margin: -10px -5px; }
			.g-box__footer-col {
				@include sm {
					padding: 10px 5px;
					width: 50%; } }
			.g-box__btn {
				@include sm {
					font-size: 12px; } } }
		+ .g-box__btn {
			@include md {
				float: right;
				width: 50%; } } }
	//centering
	&__vertical {
		@include vertical; }
	&__center {
		display: inline-block;
		vertical-align: middle;
		overflow: auto;
		width: 100%;
		max-height: 100%;
		@include sm {
			height: 100%; }
		&_noscroll {
			overflow: visible;
			height: auto !important; } }
	//common
	&__inner {
		overflow: auto;
		padding-left: 20px;
		padding-right: 20px;
		height: 100%;
		@include sm {
			overflow: visible;
			padding-left: 0;
			padding-right: 0; } }
	&__container {
		position: relative;
		padding: 0 30px;
		margin: 0 auto;
		max-width: 1024px;
		background: $white;
		box-shadow: 0 5px 25px -5px rgba(39, 55, 74, 0.15), 0 5px 20px -10px rgba(39, 55, 74, 0.1);
		text-align: left;
		@include sm {
			max-width: 500px; }
		@include xs {
			padding: 0 20px; }
		&_sm {
			width: 512px; } }
	&__col {
		display: inline-block;
		vertical-align: middle;
		@include sm {
			display: block;
			width: 100%; } }
	&__video {
		padding: 20px 30px 20px 0;
		width: 60%;
		@include sm {
			padding: 20px 0 0; }
		&-in {
			position: relative;
			padding-bottom: 56.2%; }
		iframe {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%; } }
	&__content {
		width: 40%;
		&_wide {
			width: 100%; }
		&-in {
			position: relative; }
		iframe {
			width: 100%;
			height: $minHeight; } }
	&__title {
		padding: 0 60px 20px;
		margin: 0;
		font-size: 28px;
		font-weight: 700;
		line-height: 1;
		text-align: center;
		@include sm {
			padding: 20px; }
		&_simple {
			padding: 0 0 10px;
			@include sm {
				padding: 0 30px 10px; } } }
	&__description {
		padding-bottom: 20px;
		color: $darkGray;
		font-size: 14px;
		line-height: 1.3;
		text-align: center; }
	&__close {
		position: absolute;
		z-index: 20;
		top: 0;
		right: 0;
		padding: 20px;
		border: none;
		color: $darkGray;
		opacity: 0.7;
		background: none;
		font-size: 0;
		transition: opacity 0.25s;
		@include hover {
			opacity: 1; }
		i {
			font-size: 32px; } } }
