.v-guide-link {
	display: inline-block;
	vertical-align: top;
	outline: none !important;
	color: $darkGray;
	background: none;
	font-size: 22px;
	line-height: 31px;
	text-decoration: underline;
	transition: color 0.25s;
	@include sm {
		width: 53px;
		height: 53px;
		font-size: 0;
		line-height: 53px; }
	&:hover {
		color: $blue; }
	.fa-angle-left {
		margin-right: 8px;
		@include sm {
			display: inline-block;
			vertical-align: middle;
			font-size: 28px; } } }
