.v-guide-selection {
	overflow: hidden;
	box-shadow: 0 15px 45px -5px rgba(39,55,74,.15), 0 15px 40px -20px rgba(39,55,74,.1);
	font-size: 0;
	text-align: left;
	&__items {
		padding: 0;
		margin: -1px; }
	&__item {
		position: relative;
		display: inline-block;
		vertical-align: top;
		border: 1px solid $white;
		// width: 25%
		width: 20%;
		&_special {
			.v-guide-selection {
				&__container {
					color: $darkGray !important;
					// background: #f5f0f0
					background: $white;
					text-shadow: none; } } } }
	&__container {
		position: relative;
		display: block;
		padding: 15px 15px 60px;
		// height: 230px
		height: 210px;
		color: $white !important;
		opacity: 0.7;
		background-position: left bottom;
		background-repeat: no-repeat;
		font-weight: 700;
		text-shadow: 1px 1px #333;
		text-decoration: none !important;
		cursor: pointer;
		@include gpu;
		transition: opacity 0.25s;
		@include rh($demoBreakpointSM) {
			height: 95px;
			padding-top: 10px;
			padding-bottom: 50px; }
		@include rh($demoBreakpoint) {
			height: 150px; }
		&:hover {
			opacity: 1; } }
	&__title {
		padding-bottom: 10px;
		// font-size: 24px
		font-size: 16px;
		line-height: 26px;
		@include rh($demoBreakpointSM) {
			// font-size: 20px
			line-height: 1.2; }
		@include rh($demoBreakpoint) {
			padding: 0; } }
	&__list,
	&__text {
		padding: 0;
		// font-size: 14px
		// line-height: 21px
		font-size: 12px;
		line-height: 1.3;
		@include rh($demoBreakpoint) {
			display: none; } }
	&__list {
		li {
			&:before {
				content: '• '; } } }
	&__text {
		p {
			padding: 0;
			margin: 0; } }
	&__list,
	&__list li,
	&__items,
	&__item {
		list-style-type: none; }
	&__meta {
		position: absolute;
		right: 15px;
		bottom: 10px;
		padding: 13px 31px 13px 16px;
		border-radius: 3px;
		color: $white;
		background: $blue;
		font-size: 14px;
		font-weight: 700;
		line-height: 20px;
		text-transform: uppercase;
		pointer-events: none;
		@include gpu;
		@include rh($demoBreakpointSM) {
			padding: 5px 31px 5px 10px; }
		&:after {
			content: '›';
			position: absolute;
			top: 9px;
			right: 10px;
			font-size: 2.2em;
			font-weight: normal;
			@include rh($demoBreakpointSM) {
				top: 1px; } } } }
