.v-guide-info {
	color: $gray;
	text-align: center;
	&__price {
		position: relative;
		display: inline-block;
		vertical-align: top;
		padding: 15px 0;
		line-height: 1;
		@include sm {
			padding: 0 0 15px; }
		&-main {
			color: $darkGray;
			small,
			strong {
				display: inline-block;
				vertical-align: bottom; }
			small {
				font-size: 36px;
				@include sm {
					font-size: 28px; } }
			strong {
				font-size: 48px;
				line-height: 44px;
				@include sm {
					font-size: 36px;
					line-height: 0.9; } } }
		&-label {
			display: block;
			padding: 4px 0;
			font-size: 16px;
			text-align: center;
			white-space: nowrap; } }
	&__disclaimer {
		margin: 0 auto;
		// max-width: 260px
		color: $darkGray;
		font-size: 16px;
		font-weight: 400;
		line-height: 1.3; } }
