.v-guide-btn {
	display: inline-block;
	vertical-align: top;
	// padding: 18px 20px 19px
	padding: 0 20px;
	margin: 0;
	border: none;
	border-radius: 3px;
	height: 53px;
	color: $white;
	background: $blue;
	font-size: 16px;
	line-height: 1;
	text-align: center;
	transition: background 0.25s;
	@include sm {
		padding-left: 5px;
		padding-right: 5px; }
	&:hover {
		background: #1B79B2;
		.fa-angle-right {
			transform: translate3d(5px,0,0); } }
	&:active {
		background: #286090; }
	&:disabled {
		opacity: 0.5;
		cursor: not-allowed; }
	&_wide {
		width: 100%; }
	&_gray {
		background: $gray;
		&:hover {
			background: darken($gray, 5%); }
		&:active {
			background: darken($gray, 10%); } }
	.fa {
		@include gpu;
		transition: transform 0.25s;
		@include sm {
			display: none; } } }
