.v-guide-form {
	position: relative;
	padding: 30px;
	margin: 0 auto;
	max-width: 1024px;
	background: $white;
	box-shadow: 0 5px 25px -5px rgba(39,55,74,.15), 0 5px 20px -10px rgba(39,55,74,.1);
	text-align: left;
	@include rh($formBreakpointSM) {
		padding: 20px 30px; }
	@include sm {
		padding-left: 20px;
		padding-right: 20px; }
	&__title {
		margin-bottom: 20px;
		font-size: 22px;
		font-weight: 700;
		line-height: 1.2;
		@include rh($formBreakpointSM) {
			margin-bottom: 5px; } }
	&__label {
		padding-bottom: 5px;
		font-size: 16px;
		span {
			color: $red; } }
	&__row,
	&__fields {
		@include clr; }
	&__row {
		position: relative;
		margin: 0 -20px; }
	&__fields {
		margin: 0 -10px; }
	&__col,
	&__field {
		position: relative;
		width: 50%;
		@include sm {
			width: 100%; }
		&_wide {
			width: 100%; } }
	&__col {
		padding: 0 20px; }
	&__values {
		float: right;
		@include md {
			width: 60%; } }
	&__vertical {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		@include vertical;
		border-right: 2px solid rgba(#c1cbd4, 0.3);
		@include md {
			width: 40%; }
		@include sm {
			position: relative;
			margin-bottom: 20px;
			border: none;
			border-bottom: 2px solid rgba(#c1cbd4, 0.3); } }
	&__center {
		display: inline-block;
		vertical-align: middle;
		width: 100%; }
	&__field {
		float: left;
		padding: 10px;
		@include rh($formBreakpoint) {
			padding: 7px 10px; }
		&_wide {
			width: 100%; }
		&_sm-small {
			@include rh($formBreakpoint) {
				width: 50%; } } }
	&__date {
		float: left;
		padding: 0 10px;
		width: 50%; }
	.select {
		width: 100%; }
	.select,
	input {
		font-size: 18px; }
	&__error {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		padding: 0 10px;
		margin-top: -5px;
		font-size: 12px;
		line-height: 1; }
	&__disclaimer {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 0 20px;
		font-size: 14px;
		line-height: 16px;
		text-align: left;
		@include xs {
			position: relative;
			padding: 0 0 20px; } } }
